import * as axios from "axios";

const instance = axios.create({
    baseURL: `http://www.filltext.com/`
});

export const tableApi = {
    getData(itemsCount) {
        return instance.get(`?rows=${itemsCount}&id={number|1000}&firstName={firstName}&lastName={lastName}&email={email}&phone={phone|(xxx)xxx-xx-xx}&address={addressObject}&description={lorem|32}`)
    }
}